<template>
  <div>
    <div class="player-contoiner">
      <div class="hand"></div>
      <transition name="myBox">
        <div class="my-player" v-show="seen">
          <div class="my-player-load">
            <div class="headImg">
              <a href="javascript:void(0)" @click="goaway">
                <img :src="audioList.thumb?audioList.thumb.list:'' || defaultUserImg" alt>
              </a>
            </div>
            <div class="btns">
              <a href="javascript:void(0)" title="上一首" @click="last()">
                <span class="iconfont icon-arrowleft"></span>
              </a>
              <a href="javascript:void(0)" title="播放/暂停" @click="playAudio" v-show="!isPlay">
                <span class="iconfont icon-greyPlay btns_center"></span>
              </a>
              <a href="javascript:void(0)" title="播放/暂停" @click="pauseAudio" v-show="isPlay">
                <span class="iconfont icon-greyPlaying btns_center"></span>
              </a>
              <a href="javascript:void(0)" title="下一首" @click="next()">
                <span class="iconfont icon-arrowright"></span>
              </a>
            </div>
            <div class="play">
              <div class="j-flag">
                <!--播放列表-->
                <span class="j-flag-title">{{audioList.title}}&nbsp;{{audioList.chapterTitle}}</span>
              </div>
              <div class="m-pbar">
                <!-- 进度条 -->
                <div class="barbg">
                  <div class="block">
                    <el-slider v-model="processVal" @change="onChange" :show-tooltip="false">></el-slider>
                  </div>
                </div>
                <span class="currentTime">{{dTime}}</span>
                <span class="durationTime">{{cTime}}</span>
              </div>
            </div>
            <div class="oper">
              <a href="javascript:void(0)" @click="collect">
                <span class="iconfont icon-shoucang"></span>
              </a>
              <a href="javascript:void(0)" @click="shareIng">
                <span class="iconfont icon-fenxiang"></span>
              </a>
              <a href="javascript:void(0)" @click="volumShow">
                <img src="../assets/img/yinliang.png" alt>
              </a>
              <a href="javascript:void(0)">
                <!-- <img src="../assets/img/line.png" @click="handClick"> -->
                <span class="iconfont icon-list" @click="handClick()"></span>
              </a>
            </div>
            <div class="list" v-show="isShow " id="div1">
              <div class="list-top">
                <span>播放列表</span>
              </div>
              <div class="list-line"></div>
              <div class="list-bottom">
                <ul>
                  <li
                    class="putOff"
                    v-for="(item,i) in list.chapters"
                    :key="i"
                    :class="i==index?'putOn':''"
                    @click="toListen(item,i)"
                  >
                    <span class="jing">{{audioList.title}}&nbsp;&nbsp;&nbsp;&nbsp;{{item.title}}</span>
                    <a href="javascript:void(0)">
                      <span class="t-kan" @click.stop="toListen(item,i)">听与看</span>
                    </a>
                  </li>
                </ul>
              </div>
            </div>
            <div class="volume" v-show="volum">
              <div class="block" style="margin-top:14px">
                <el-slider
                  v-model="value"
                  vertical
                  height="85px"
                  @change="volumeChange"
                  :format-tooltip="formatVolume"
                ></el-slider>
              </div>
            </div>
          </div>
        </div>
      </transition>
    </div>
    <el-dialog
      :title="qrTitle"
      :visible.sync="shareShow"
      id="scanDialoge"
      :close-on-click-modal="false"
      show-close
    >
      <QrcodeVue :value="qrText" size="200" class="qrCode"/>
      <!-- <img id="qrCode" :src="imgUrl" alt> -->
      <p>{{tipInfo}}</p>
    </el-dialog>
  </div>
</template>
<script>
import QrcodeVue from "qrcode.vue";
export default {
  data() {
    return {
      index: 999,
      cTime: "00:00", //音频当前播放时间
      dTime: "00:00", //音频总播放时间
      processVal: 0, //音频进度百分比
      value: 100,
      audioAllTime: 0, //音频总播放秒数
      isPlay: false,
      isShow: false,
      volum: false,
      interval: "",
      qrTitle: "",
      tipInfo: "",
      qrText: "",
      shareShow: false
    };
  },
  mounted() {
    var audio = document.getElementById("myAudio");

    var vm = this;
    this.setAudioInterval();
    audio.onplay = function() {
      // console.log("开始");
      vm.setUpdate("state");
      vm.setInterval();
      vm.$store.commit("setFlag", true);
      
    };
    audio.onpause = function() {
      // console.log("播放暂停");
      clearInterval(vm.interval);
    };
    audio.onended = function() {
      // console.log("播放完毕");
      vm.setUpdate("end");
      clearInterval(vm.interval);
    };
  },
  methods: {
    handClick() {
      this.isShow = !this.isShow;
    },
    volumShow() {
      this.volum = !this.volum;
    },
    getAudioList() {
      var Text = document.getElementById("text");
      var audio = document.getElementById("myAudio");
      // console.log(161,audio)
      var _this = this;
      var url = this.APIURL + "/classic/classic/chapter/detail";
      this.$axios
        .post(
          url,
          {
            classicId: this.classicId,
            chapterId: this.chapterId
          },
          { headers: { SessionKey: this.SessionKey, key:this.key } }
        )
        .then(function({ data }) {
          //如果返回的数据没有source则提示没有音频
          if (data.data.source) {
            _this.$store.commit("setAudioList", data.data);
            audio.src = _this.audioList.source.sq;
            Text.innerHTML = _this.audioList.content;
            // audio.currentTime = data.data.position;
          } else {
            _this.$message({
              message: "音频地址出错喽！",
              type: "error"
            });
          }
          audio.onloadeddata = function() {
            // console.log(audio.duration);
          };
        });
    },
    //设置定时播放进度
    setInterval(data) {
      const vm = this;
      this.interval = setInterval(function() {
        vm.setUpdate("on");
      }, 6000);
    },
    //同步播放进度
    setUpdate(type) {
      var _this = this;
      var audio = document.getElementById("myAudio");
      // console.log(audio.currentTime)
      var url = this.APIURL + "/classic/classic/sync";
      let data = {
        classicId: this.audioList.classicId,
        chapterId: this.audioList.id,
        position: audio.currentTime,
        type: type
      };
      this.$axios
        .post(url, data, { headers: { SessionKey: this.SessionKey,key:this.key } })
        .then(function(data) {});
    },
    //点击听与看
    toListen(item, i) {
      this.index = i;
      var _this = this;
      this.$store.commit("setClassicId", this.list.id);
      this.$store.commit("setChapterId", item.id);
    },
    setAudioInterval() {
      this.interval = setInterval(() => {
        this.getAudioTime();
        var audio = document.getElementById("myAudio");
        if (audio.ended) {
          //播放结束后重置数据
          clearInterval(this.interval);
          this.processVal = 0;
          audio.currentTime = 0;
          this.cTime = "00:00";
          this.isPlay = false;
          this.i = 0;
        }
        audio.paused ? (this.isPlay = false) : (this.isPlay = true);
      }, 1000);
    },
    //播放
    playAudio() {
      //重设定时器
      // clearInterval(audioInterval);
      this.getAudioTime();
      this.setAudioInterval();
      document.getElementById("myAudio").play();
      this.isPlay = true;
      this.$store.commit("setFlag", true);
    },
    //暂停
    pauseAudio() {
      document.getElementById("myAudio").pause();
      this.isPlay = false;
      this.$store.commit("setFlag", false);
    },
    //上一首
    last() {
      var previous = this.audioList.previous;
      if (previous) {
        this.$store.commit("setChapterId", previous);
      } else {
        this.$message({
          message: "没有上一首喽！",
          type: "error"
        });
      }
    },
    next() {
      var next = this.audioList.next;
      if (next) {
        this.$store.commit("setChapterId", next);
      } else {
        this.$message({
          message: "没有下一首喽！",
          type: "error"
        });
      }
    },
    goaway() {
      this.$router.push({ path: "/classic/details" });
    },
    shareIng() {},
    //获取播放时间
    getAudioTime() {
      var audio = document.getElementById("myAudio");
      this.cTime = this.realFormatSecond(audio.duration);
      this.audioAllTime = audio.duration;

      //计算当前进度百分比

      // this.processVal = (this.audioList.position / audio.duration) * 100;
      // this.dTime = this.realFormatSecond(this.audioList.position);

      this.processVal = (audio.currentTime / audio.duration) * 100;
      //  console.log(this.processVal)
      this.dTime = this.realFormatSecond(audio.currentTime);
    },
    //滑动进度条
    onChange(value) {
      var audio = document.getElementById("myAudio");

      this.dTime = this.realFormatSecond((this.audioAllTime * value) / 100);
      audio.currentTime = parseInt((this.audioAllTime * value) / 100);
    },
    //音量的滑动条
    volumeChange(val) {
      console.log(val);
      var audio = document.getElementById("myAudio");
      audio.volume = val / 100;
    },
    formatVolume(val) {
      return val;
    },
    //转为00:00格式
    realFormatSecond: function(second) {
      var secondType = typeof second;
      if (secondType === "number" || secondType === "string") {
        second = parseInt(second);
        // var hours = Math.floor(second / 3600)
        // second = second - hours * 3600
        second = second;
        var mimute = Math.floor(second / 60);
        second = second - mimute * 60;
        return ("0" + mimute).slice(-2) + ":" + ("0" + second).slice(-2);
      } else {
        return "00:00";
      }
    },
    // 分享
    shareIng() {
      this.shareShow = true;
      this.qrText =
        "https://m.lihan.ltd/#/pages/mantra/classicDetail?curId=" +
        this.list.id +
        "&app=false&bgclr=false";
      this.qrTitle = "微信扫描二维码进行分享";
    },
    // 收藏
    collect() {
      var url = this.APIURL + "/classic/classic/behavior";
      let data = {
        classicId: this.list.id,
        type: "collect"
      };
      this.$axios
        .post(url, data, { headers: { SessionKey: this.SessionKey,key:this.key } })
        .then(res => {
          if (res.data.errorCode == 0) {
            this.$message({
              message: "收藏成功",
              type: "success"
            });
          } else {
            alert(res.data.msg);
            this.$router.push({ path: "/login" });
          }
        })
        .catch(err => {});
    }
  },
  watch: {
    classicId: function() {},
    chapterId: function(val) {
      this.getAudioList();
    },
    // list: function(val) {},
    seen: function() {}
  },
  computed: {
    chapterId() {
      return this.$store.state.chapterId;
    },
    list() {
      // console.log(this.$store.state.bookList);
      return this.$store.state.bookList;
    },
    audioList() {
      // console.log(this.$store.state.audioList);
      return this.$store.state.audioList;
    },
    seen() {
      return this.$store.state.seen;
    },
    classicId() {
      // console.log(this.$store.state.classicId)
      return this.$store.state.classicId;
    },
    volume() {
      return this.$store.state.volume;
    },
    SessionKey() {
      return this.$store.state.SessionKey;
    },
    key() {
      return this.$store.state.key;
    }
  },
  components: {
    QrcodeVue
  }
};
</script>
<style lang="scss" >
.player-contoiner {
  position: fixed;
  width: 100%;
  bottom: 0;
  left: 0;
  z-index: 10;
  .my-player {
    width: 100%;
    height: 60px;
    opacity: 0.9;
    margin-top: 20px;
    background: #333333;

    .my-player-load {
      width: 1000px;
      height: 50px;
      margin: 0 auto;
      position: relative;
      .headImg {
        position: absolute;
        left: 0;
        top: 0;
        color: #fff;
        img {
          width: 50px;
          height: 50px;
          opacity: 1;
          margin: 5px 0 0 12px;
        }
      }
      .btns {
        position: absolute;
        left: 80px;
        overflow: hidden;
        a {
          text-decoration: none;
          .iconfont {
            color: #fff;
            font-size: 30px;
            margin-right: 8px;
            line-height: 60px;
          }
        }
      }
      .play {
        position: absolute;
        left: 210px;
        .j-flag {
          position: fixed;
          span {
            opacity: 1;
            font-size: 14px;
            font-family: Arial, Helvetica, sans-serif;
            color: rgba(255, 255, 255, 1);
            letter-spacing: 0px;
            line-height: 28px;
          }
        }
        .m-pbar {
          top: 30px;
          position: relative;
          .barbg {
            width: 400px;
            height: 3px;
            .j-flag {
              span {
                position: absolute;
                top: -4px;
                display: block;
                left: 0;
                width: 9px;
                height: 9px;
                opacity: 1;
                background: rgba(255, 255, 255, 1);
                border-radius: 50%;
              }
            }
          }
          .currentTime {
            position: absolute;
            display: block;
            top: -12px;
            right: -48px;
            opacity: 1;
            font-size: 15px;
            font-family: Adobe Heiti Std;
            color: rgba(255, 255, 255, 1);
            letter-spacing: 0px;
          }
          .durationTime {
            position: absolute;
            display: block;
            top: -12px;
            right: -95px;
            opacity: 1;
            font-size: 15px;
            font-family: Adobe Heiti Std;
            color: rgba(255, 255, 255, 1);
            letter-spacing: 0px;
          }
        }
      }
      .oper {
        position: absolute;
        left: 730px;
        top: 18px;
        a {
          margin-right: 14px;
          text-decoration: none;
          .iconfont {
            font-size: 18px;
            color: #fff;
          }
          img {
            width: 16px;
            height: 13px;
          }
        }
      }
    }
  }
  .list {
    width: 1000px;
    height: 450px;
    background: #121212;
    position: absolute;
    top: -450px;
    margin: 0 auto;
    overflow: hidden;
    .list-top {
      span {
        opacity: 1;
        font-size: 13px;
        font-family: Adobe Heiti Std;
        color: rgba(255, 255, 255, 1);
        letter-spacing: 0px;
        margin: 12px 0 0 30px;
        display: block;
      }
    }
    .list-line {
      width: 940px;
      height: 1px;
      opacity: 1;
      background: rgba(102, 102, 102, 1);
      margin: 12px auto;
    }
    .list-bottom {
      padding: 6px 7px 6px 11px;
      width: 100%;
      height: 396px;
      overflow: auto;
      overflow-y: scroll;
      overflow-x: hidden;
      ul {
        padding: 0;
        position: relative;
        .putOff {
          list-style: none;
          width: 100%;
          height: 50px;
          line-height: 50px;
          .jing {
            opacity: 1;
            font-size: 14px;
            font-family: Microsoft YaHei;
            color: rgba(255, 255, 255, 1);
            letter-spacing: 0px;
            margin-left: 20px;
          }
          .ting {
            opacity: 1;
            font-size: 14px;
            font-family: Microsoft YaHei;
            color: rgba(153, 153, 153, 1);
            letter-spacing: 0px;
            margin-left: 32px;
          }
          .t-kan {
            opacity: 1;
            font-size: 14px;
            font-family: Microsoft YaHei;
            color: rgba(255, 255, 255, 1);
            letter-spacing: 0px;
            position: absolute;
            right: 32px;
          }
        }
        .putOn {
          list-style: none;
          width: 100%;
          height: 50px;
          line-height: 50px;
          background: #333333;
          opacity: 0.7;
        }
      }
      &::-webkit-scrollbar {
        width: 10px; /*高宽分别对应横竖滚动条的尺寸*/
        height: 1px;
      }
      &::-webkit-scrollbar-thumb {
        /*滚动条里面小方块*/
        border-radius: 10px;
        background: #535353;
      }
      &::-webkit-scrollbar-track {
        /*滚动条里面轨道*/

        border-radius: 10px;
        background: #121212;
      }
    }
  }
  .volume {
    position: absolute;
    width: 32px;
    height: 113px;
    background-color: #000000;
    top: -113px;
    right: 182px;
    z-index: 666;
  }
}
.hand {
  width: 100%;
  height: 20px;
}
.myBox-leave-active,
.myBox-enter-active {
  transition: all 1s ease;
}
.myBox-leave-active,
.myBox-enter {
  height: 0px !important;
}
.myBox-leave,
.myBox-enter-active {
  height: 40px;
}

.el-slider__button {
  width: 8px;
  height: 8px;
  border: none;
}
.el-slider__runway {
  margin: 0;
}
.el-slider__bar {
  background-color: #ee8834;
  height: 4px;
}
.el-slider__runway {
  height: 4px;
  background-color: #1a1a1a;
}
.el-slider__button-wrapper {
  top: -16px;
}
.el-slider.is-vertical .el-slider__runway {
  width: 3px;
}
.el-slider.is-vertical .el-slider__bar {
  width: 3px;
}
.el-slider.is-vertical .el-slider__button-wrapper {
  left: -16px;
}
</style>
