<template>
  <div class>
    <div class="header">
      <div class="userInfo">
        <!-- <div class="enter">
          <a href="javascript:void(0)">罕语经韵</a>
          <a href="javascript:void(0)">钦赐龙藏</a>
          <a href>进入后台</a>
        </div>-->
        <div class="userName">
          <div class="username">{{SessionKey || key?userInfo.nickname: '游客'}}</div>
          <div class="headerImg">
            <img :src="userInfo.avatar" alt v-if="!!SessionKey">
            <span class="iconfont icon-lihansongjing questionAvatar" v-else></span>
          </div>
          <div class="isLogin">
            <el-dropdown>
              <span class="el-dropdown-link">
                <i class="el-icon-arrow-down el-icon--right"></i>
              </span>
              <el-dropdown-menu slot="dropdown">
                <el-dropdown-item v-if="userInfo" @click.native="quit">退出</el-dropdown-item>
                <el-dropdown-item v-else @click.native="login">登录</el-dropdown-item>
                <el-dropdown-item @click.native="goaway" v-show="!!SessionKey">个人中心</el-dropdown-item>
              </el-dropdown-menu>
            </el-dropdown>
          </div>
        </div>
      </div>
    </div>
    <!-- <setQrShow/> -->
  </div>
</template>
<script>
// import setQrShow from "../login/login"
export default {
  data() {
    return {
      onshow: false,
      name: "",
      UserInfoList: ""
    };
  },
  mounted() {},
  methods: {
    login() {
      // this.$store.commit("setQrShow", true);
      this.$router.push({ path: "/login" });
    },
    quit() {
      this.$store.commit("setUserInfo", "");
      this.$store.commit("setSessionKey", "");
      this.$store.commit("setKey", "");
      window.localStorage.removeItem("sessionId"); //清除文件userInfo
      window.localStorage.removeItem("key"); //清除文件userInfo
      window.localStorage.removeItem("userInfo");
      this.$router.push("/index");
    },
    goaway() {
      this.$router.push({ path: "/personal" });
    }
  },
  computed: {
    userInfo() {
      console.log(this.$store.state.UserInfo)
      return this.$store.state.UserInfo;
    },
    SessionKey() {
      return this.$store.state.SessionKey;
    },
    key() {
      return this.$store.state.key;
    },
  },
  watch: {
    // SessionKey(val) {
    //   console.log(val);
    //   if (val) {
    //     this.onshow = true;
    //   } else {
    //     this.onshow = false;
    //   }
    // }
  },
  components: {
    //当前组件内部使用过的子组件
    // setQrShow
  }
};
</script>

