<template>
  <div class>
    <div class="search bar">
      <div class="logo">
        <span class="iconfont iconhanyujingyun theme-text"></span>
        <div class="dec-logo">
          <!-- <div class="left"> -->
          <!-- <img src="https://mhb.hanyujingyun.cn/images/lihansongjing.svg"> -->
          <span class="iconfont icon-lihansongjing theme-text"></span>
          <!-- </div> -->

          <span class="iconfont icon-li- theme-text"></span>
          <span class="iconfont icon-han- theme-text"></span>
          <span class="iconfont icon-song- theme-text"></span>
          <span class="iconfont icon-jing- theme-text"></span>
        </div>
      </div>
      <div class="submit">
        <el-autocomplete
          class="inline-input"
          v-model="keywords"
          :fetch-suggestions="querySearch"
          placeholder="请输入内容"
          @select="handleSelect"
        ></el-autocomplete>

        <button type="submit" class="theme-bg" @click="searchIng">
          <span class="iconfont icon-search2 lightest-text" id="search"></span>
        </button>
      </div>
      <!-- <div class="user">
        <div class="headerImg">
          <img :src="UserInfo.avatar" alt v-if="UserInfo.avatar !=null">
          <span class="iconfont icon-user2 questionAvatar theme-text " v-else></span>
        </div>
       
        <div class="isLogin">
          <el-dropdown>
            <span class="el-dropdown-link">
              <i class="el-icon-arrow-down el-icon--right"></i>
            </span>
            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item v-if="UserInfo.nickname" @click.native="quit">退出</el-dropdown-item>
              <el-dropdown-item v-else @click.native="login">登录</el-dropdown-item>
              <el-dropdown-item>个人中心</el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
        </div>
      </div>-->
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      restaurants: [],
      keywords: ""
    };
  },
  mounted() {},
  methods: {
    searchIng() {
      var _this = this;
      let data = {
        keywords: _this.keywords,
        channel: 12,
        type: ""
      };
      var url = this.APIURL + "/classic/home/search";
      this.$axios
        .post(url, data)
        .then(function(res) {
          if (_this.keywords) {
            _this.$router.push("/searchResult");
            _this.$store.commit("setSearchResult", res.data.data);
          } else {
            return false;
          }
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    querySearch(queryString, cb) {
      var _this = this;
      var url = this.APIURL + "/classic/home/search_list";
      this.$axios.post(url).then(function({ data }) {
      var loadAll = data.data.hot;
      var devEuiArr=[];
        for (var i = 0; i <loadAll.length; i++) {
          devEuiArr.push({'value':loadAll[i]})
        }
        cb(devEuiArr);
      });
    },
    createFilter(queryString) {
      return restaurant => {
        return (
          restaurant.value.toLowerCase().indexOf(queryString.toLowerCase()) ===
          -1
        );
      };
    },
    handleSelect(item) {
      console.log(item);
    }
  },
  computed: {}
};
</script>
<style lang="scss" scoped>
